@font-face{
	font-family: 'Yekan';
	src:	url('./font/Yekan.eot'); /* IE9 Compat Modes */
	src:	url('./font/Yekan.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
			url('./font/Yekan.woff2') format('woff2'), /* Modern Browsers */
			url('./font/Yekan.woff') format('woff'), /* Modern Browsers */
			url('./font/Yekan.otf') format('opentype'), /* Open Type Font */	
			url('./font/Yekan.ttf') format('truetype'); /* Safari, Android, iOS */
	font-weight: normal;
	font-style: normal;
	text-rendering: optimizeLegibility;
	font-display: auto;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Yekan';
    transition: .3s ease;
}
/* color and styles (default) */
:root{
    --PrimaryColor: hsl(94, 59%, 35%);
    --HoverColor: #bdf094;
    --paleGreen: hsl(96, 75%, 89%);
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(0, 0%, 18%);
    --textColor: hsl(240, 1%, 48%);
    --bgColor: hsl(220, 10%, 94%);
    --greyText: rgb(190, 190, 190);
    --inputColor: hsl(330, 12%, 97%);
    /* fonts and typography */
    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.5rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: .938rem;
    --smallFontSize: .813rem;
    --smallestFontSize: .75rem;
}
/* defual styels */
html{
    font-size: 90%;
}
a{
    text-decoration: none;
}
li{
    list-style: none;
}
.icon{
    font-size: var(--h2FontSize);
    cursor: pointer;
}
img{
    width: 100%;
    height: auto;
}
.flex{
    display: flex;
    align-items: center;
}
.grid{
    display: grid;
    align-items: center;
    gap: 1.5rem;
}
.btn{
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 8px;
    box-shadow: 0 2px 4px var(--inputColor);
    padding: .8rem 1rem;
}
.btn .icon{
    margin-left: .5rem;
}
.btn:hover{
    background: var(--paleGreen);
    color: var(--PrimaryColor);
}
body{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 1rem;
}
.container{
    position: relative;
    width: 95vw;
    height: 95vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bgColor);
    overflow: hidden;
    border-radius: 1rem;
    box-shadow: -2px 2px 8px var(--greyText);
}
/* hide the scroll thumb */
::-webkit-scrollbar{
    display: none;
}
/* media queries  */
@media screen and (max-width: 990px){
    .heading{
        flex-direction: column;
        align-items: flex-start;
        gap: 1rem;
    }
}
@media screen and (max-width: 918px){
    .heading{
        flex-direction: row;
        align-items: center;
        gap: 1rem;
    }
}
@media screen and (max-width: 500px){
    .heading{
        flex-direction: column;
        align-items: flex-start;
        gap: 1rem;
    }
}
